var hiragana = require('src/lib/hiragana.i18n.json');
// var romaji = require('src/lib/romaji.i18n.json');
var katakana = require('src/lib/katakana.i18n.json');

import store from 'src/reducers';

export const changeLanguage = () => {
    let data: any = store.getState();
    switch (data.settings.language) {
        case 'hiragana':
            return hiragana;
        case 'katakana':
            return katakana;
        default:
            return hiragana;
    }
};


