import { Dispatch } from 'redux';

import { IDisplayInputHistoryProps } from 'src/interface';

export const addHistory = (displayInput: IDisplayInputHistoryProps) => {
    if (displayInput.romaji.length > 0) {
        return (dispatch: Dispatch<Object>) => {
            dispatch({
                type: 'ADD_HISTORY',
                displayInput
            });
        };
    }
    return (dispatch: Dispatch<Object>) => {
        // dispatch({
        //     type: 'ADD_HISTORY',
        //     displayInput
        // });
    };
};

export const loadHistory = (displayInput: IDisplayInputHistoryProps[]) => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'LOAD_HISTORY',
            displayInput
        });
    };
};

export const storeTime = () => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'STORE_TIME'
        });
    };
};


