import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { changeSettings } from 'src/actions';
import { ISettingsProps } from 'src/interface';

interface IChangeLanguageProps {
    settings: ISettingsProps;
    changeSettings: (level: any) => Dispatch<Object>;
}

class ChangeLanguage extends React.Component<IChangeLanguageProps, {}> {
    changeLanguage = (language: string) => {
        this.props.changeSettings({ language });
    }
    render() {
        return (
            <div>
                <button className="change-language-button" onClick={() => this.changeLanguage('hiragana')}>Hiragana</button>
                <button className="change-language-button" onClick={() => this.changeLanguage('katakana')}>Katakana</button>
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        settings: state.settings
    };
}
function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            changeSettings
        },
        dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(ChangeLanguage);


