import { Dispatch } from 'redux';

import { changeLanguage } from 'src/helper/changeLanguage'; 
// var constHiraganaObject = require('src/lib/hiragana.i18n.json');
var constRomajiObject = require('src/lib/romaji.i18n.json');
const constRomajiArray = Object.keys(constRomajiObject);

export const changeRomajiInput = (input: string, generatedRomajiList: string[]) => {
    let newInput = input;
    let inputMatch = input.match(/[a-zA-Z ]+/g);
    if (inputMatch) {
        if (inputMatch[0] !== 'n' && constRomajiArray.indexOf(inputMatch[0]) >= 0) {
            newInput = newInput.replace(inputMatch[0], changeLanguage()[inputMatch[0]]);
        } else if (inputMatch[0] === 'n ') {
            newInput = newInput.replace(inputMatch[0], changeLanguage().n);
        }
    }
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'CHANGE_ROMAJI_INPUT',
            input: newInput
        });
    };
};

export const clearRomajiInput = () => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'CLEAR_ROMAJI_INPUT'
        });
    };
};


